<template>
  <div class="home">
    <p class="title is-8">  </p>
    <p class="subtitle is-8">
         , ID     .
    </p>
    <b-field label="    " grouped>
      <b-datepicker v-model="selectedStart" :mobile-native="false">
        <template v-slot:trigger>
          <b-button icon-left="calendar" type="is-primary" />
        </template>
      </b-datepicker>
      <b-input expanded readonly :value="startString" />
      <b-datepicker v-model="selectedEnd" :mobile-native="false">
        <template v-slot:trigger>
          <b-button icon-left="calendar" type="is-primary" />
        </template>
      </b-datepicker>
      <b-input expanded readonly :value="endString" />
    </b-field>
    <b-field label="ID ">
      <div class="columns">
        <div class="column">
          <b-field label=" ID">
            <b-input
              type="text"
              v-model="uidfrom"
              :disabled="isMassiveGift"
            ></b-input>
          </b-field>
        </div>
        <div class="column">
          <b-field label=" ID">
            <b-input
              type="text"
              v-model="uidto"
              :disabled="isMassiveGift"
            ></b-input>
          </b-field>
        </div>
      </div>
    </b-field>
    <b-field label=" ">
      <b-radio v-model="isMassiveGift" name="name" :native-value="true">
         
      </b-radio>
      <b-radio v-model="isMassiveGift" name="name" :native-value="false">
          
      </b-radio>
      <b-radio v-model="isMassiveGift" name="name" :native-value="null">
         
      </b-radio>
    </b-field>
    <b-button @click="findGift" type="is-primary"></b-button>
    <Table
      v-if="loaded"
      type="gift"
      :products="products"
      :columns="columns"
      :data="data"
      :detailKey="table.detailKey"
      :checkable="true"
    />
    <table-loading v-else-if="loaded == false" />
  </div>
</template>

<script>
// @ is an alias to /src
import Table from "@/components/Table";
import TableLoading from "../components/TableLoading.vue";
export default {
  name: "Home",
  components: {
    Table,
    TableLoading,
  },
  created() {
    this.$axios
      .get(`${process.env.VUE_APP_API_URL}/product`)
      .then((productRes) => {
        if (productRes.data.status) {
          this.products = productRes.data.list
          if (this.$route.query.orderId != null) {
            this.findGift();
          }
        }
      });
  },
  methods: {
    findGift() {
      this.loaded = false;
      let data = {};
      if (this.isMassiveGift == true) {
        this.uidfrom = "massiveGift";
      }
      if (this.selectedStart != null && this.selectedEnd != null) {
        console.log("  ");
        //   
        if (this.uidfrom != null || this.uidto != null) {
          console.log("ID ");
          // ID   
          data = {
            type: "all",
            start: this.selectedStart,
            end: this.selectedEnd,
            uidfrom: this.uidfrom,
            uidto: this.uidto,
          };
        } else {
          console.log("ID ");

          //   
          data = {
            type: "date",
            start: this.selectedStart,
            end: this.selectedEnd,
          };
        }
      } else if (this.selectedStart == null && this.selectedEnd == null) {
        console.log("  ");

        //   
        if (this.uidfrom != null || this.uidto != null) {
          data = {
            type: "uid",
            uidfrom: this.uidfrom,
            uidto: this.uidto,
          };
          // ID   
          console.log("ID ");
        } else {
          //   
          data = {
            type: "null",
          };
          console.log("ID ");
        }
      }
      console.log(data);
      this.$axios
        .post(`${process.env.VUE_APP_API_URL}/gift/filter`, data)
        .then((giftRes) => {
          if (giftRes.data.status) {
            this.data = giftRes.data.result;
            console.log(this.isMassiveGift);
            if (this.isMassiveGift == false) {
              this.data = this.data.filter(
                (gift) => gift.uidfrom != "massiveGift"
              ).reverse();
            }
            this.loaded = true;
          }
        });
    },
  },
  computed: {
    startString() {
      return this.selectedStart ? this.selectedStart.toLocaleDateString() : "";
    },
    endString() {
      return this.selectedEnd ? this.selectedEnd.toLocaleDateString() : "";
    },
  },
  data() {
    return {
      isMassiveGift: false,
      loaded: null,
      selectedStart: null,
      selectedEnd: null,
      uidfrom: null,
      uidto: null,
      data: [],
      products: [],
      table: {
        detailKey: "orderId",
      },
      columns: [
        {
          field: "orderId",
          label: "",
        },
        {
          field: "uidfrom",
          label: " ID",
        },
        {
          field: "uidto",
          label: " ID",
        },
        {
          field: "amount",
          label: "",
        },
        {
          field: "date",
          label: " ",
        },
        {
          field: "expire",
          label: "  ",
        },
        {
          field: "exchanged",
          label: " ",
        },
      ],
    };
  },
};
</script>
