<template>
  <div class="home">
    <p class="title is-8"> </p>
    <p class="subtitle is-8">
         , ID     .
    </p>
    <Table
      v-if="loaded"
      type="product"
      :products="products"
      :columns="columns"
      :data="products"
      :detailKey="table.detailKey"
      :checkable="true"
    />
    <table-loading v-else />
  </div>
</template>

<script>
// @ is an alias to /src
import Table from "@/components/Table";
import TableLoading from "@/components/TableLoading.vue";
export default {
  name: "Home",
  components: {
    Table,
    TableLoading,
  },
  created() {
    this.$axios
      .get(`${process.env.VUE_APP_API_URL}/product`)
      .then((productRes) => {
        if (productRes.data.status) {
          this.products = productRes.data.list;
          this.loaded = true;
        }
      });
  },
  data() {
    return {
      loaded: false,
      data: [],
      products: [],
      table: {
        detailKey: "id",
      },
      columns: [
        {
          field: "id",
          label: " ",
        },
        {
          field: "productName",
          label: "",
        },
        {
          field: "price",
          label: "",
        },
        {
          field: "desc",
          label: "",
        },
        {
          field: "soldout",
          label: " ",
        },
      ],
    };
  },
};
</script>

<style>
.cropper {
  background: #ddd;
}
</style>