<template>
  <div id="bulkform" v-if="customer.teacherCheck">
    <p class="title is-8" style="margin-bottom: 5vh">   </p>
    <b-steps v-model="activeStep" :has-navigation="false">
      <b-step-item step="1" label=" " icon="file-download">
        <p>
                   
          .
        </p>

        <a :href="tempURL">
          <b-button
            @click="activeStep = 1"
            variant="primary"
            size="lg"
            style="margin-bottom: 20px"
            > </b-button
          ></a
        >
        <b-button
          @click="activeStep = 1"
          variant="primary"
          size="lg"
          style="margin-bottom: 20px; margin-left: 20px"
          >  .</b-button
        >
      </b-step-item>
      <b-step-item step="2" label=" " icon="keyboard">
        <p>
            ,     .
          <br />
          <span style="color: red"
            >(     '-'  
            .)</span
          >
        </p>
        <b-field label=" ">
          <b-select
            v-model="product"
            style="margin-bottom: 20px"
            size="lg"
            placeholder=" ."
          >
            <option
              v-for="(product, idx) in prodList"
              :key="idx"
              :value="product"
            >
              {{ product.text }}
            </option>
          </b-select>
        </b-field>
        <b-field label="1  ">
          <b-input
            v-model="customer.qty"
            type="number"
            size="lg"
            placeholder="1  "
            style="margin-bottom: 20px"
            maxlength="3"
          />
        </b-field>
        <b-field label=" ">
          <b-input
            v-model="customer.reason"
            type="text"
            size="lg"
            style="margin-bottom: 20px"
          />
        </b-field>
        <b-button
          @click="goToUpload"
          class="is-primary"
          size="lg"
          style="float: right"
          ></b-button
        >
      </b-step-item>
      <b-step-item step="2" label="  " icon="upload">
        <b-field label="  ">
          <b-upload v-model="file" drag-drop>
            <section class="section">
              <div class="content has-text-centered">
                <p>
                  <b-icon icon="upload" size="is-large"> </b-icon>
                </p>
                <p v-if="typeof file == 'function'">
                    
                </p>
                <div v-else>
                  {{ file.name }}
                </div>
              </div>
            </section>
          </b-upload>
        </b-field>
        <b-button @click="submit" class="is-primary" style="float: right"
          ></b-button
        >
      </b-step-item>
    </b-steps>
  </div>
</template>
<script>
export default {
  created() {
    this.$store.dispatch("loginCheckA").then(
      function () {
        if (
          this.$store.getters.getName == null ||
          this.$store.getters.getRole == null
        ) {
          this.$router.push("/login");
        } else {
          this.customer.name = this.$store.getters.getName;
          this.customer.role = this.$store.getters.getRole;
          this.customer.uid = this.$store.getters.getUid;
          if (this.customer.role == "T" || this.customer.role == "A") {
            this.customer.teacherCheck = true;
          }
        }
      }.bind(this)
    );
    this.$axios
      .get(`${process.env.VUE_APP_API_URL}/product`)
      .then((res) => {
        if (res != undefined) {
          res.data.list.forEach((item) => {
            this.prodList.push({
              value: item.id,
              text: item.productName,
            });
          });
        }
        console.log(this.prodList);
      })
      .catch((error) => {
        this.$Sentry.captureException(error);
        alert("   .  . (1)");
      });
  },
  data() {
    return {
      activeStep: 0,
      customer: {
        uid: "",
        name: "",
        role: "",
        teacherCheck: true,
        reason: "",
      },
      file: File,
      product: null,
      form: FormData,
      prodList: [],
      tempURL: process.env.VUE_APP_API_URL + "/../excelTemplate.xlsx",
    };
  },
  methods: {
    goToUpload() {
      const customer = this.customer;
      if (customer.reason == "") {
        alert("  !");
      } else if (this.product == null) {
        alert(" !");
      } else if (customer.qty <= 0) {
        alert("  ! ");
      } else if (customer.qty > 100) {
        alert("1   100 !");
      } else {
        let form = new FormData();
        form.append("senderUid", customer.uid);
        form.append("senderName", customer.name);
        form.append("product", this.product.value);
        form.append("reason", customer.reason);
        form.append("qty", customer.qty);
        this.activeStep = 2;
        this.form = form;
      }
    },
    submit() {
      console.log(this.form);
      console.log(this.file);
      if (typeof this.file == "function") {
        alert(" !");
      } else {
        this.form.append("excel", this.file);
        this.$axios
          .post(`${process.env.VUE_APP_API_URL}/bulk/request`, this.form, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })
          .then((res) => {
            console.log(res);
            if (res.data.status == true) {
              alert(" .    .");
            } else {
              alert(res.data.message);
            }
          })
          .catch((error) => {
            this.$Sentry.captureException(error);
            alert(error.status);
          });
      }
    },
  },
};
</script>
<style>
#bulk {
  padding: 30px;
  text-align: left;
  margin-bottom: 100px;
}
.upload {
  width: 100% !important;
}
.upload-draggable {
  width: 100% !important;
}
</style>
