<template>
  <div class="home">
    <p class="title is-8">  </p>
    <p class="subtitle is-8">    .</p>
    <Table
      v-if="loaded"
      type="event"
      :products="products"
      :columns="columns"
      :data="data"
      :detailKey="table.detailKey"
      :checkable="false"
      :page="table.page"
    />
  </div>
</template>

<script>
// @ is an alias to /src
import Table from "@/components/Table";
export default {
  name: "Home",
  components: {
    Table,
  },
  created() {
    this.$axios
      .get(`${process.env.VUE_APP_API_URL}/product`)
      .then((productRes) => {
        if (productRes.data.status) {
          this.products = productRes.data.list;
          this.lookup();
        }
      });
    if (this.$route.query.page == undefined) {
      if (this.$route.query.orderId) {
        this.$router.replace({ query: { page: 1 } });
      }
    } else {
      this.page = parseInt(this.$route.query.page);
    }
    let idx = this.data.findIndex(
      (element) => element.orderId == "1202109261632631132259000"
    );
    this.checkedRows[0] = this.data[idx];
    this.page = Math.ceil(idx / 10);
  },
  methods: {
    lookup() {
      this.$axios
        .get(`${process.env.VUE_APP_API_URL}/event/`)
        .then((eventRes) => {
          console.log(eventRes);
          if (eventRes.data.result != null) {
            this.data = eventRes.data.result;
            this.loaded = true;
          }
        });
    },
  },
  data() {
    return {
      loaded: false,
      data: [],
      products: [],
      table: {
        detailKey: "id",
        page: 1,
      },
      columns: [
        {
          field: "id",
          label: " ",
        },
        {
          field: "name",
          label: "",
        },
        {
          field: "amount",
          label: "",
        },
        {
          field: "start",
          label: " ",
        },
        {
          field: "end",
          label: " ",
        },
        {
          field: "totalqty",
          label: " ",
        },
        {
          field: "qty",
          label: "  ",
        },
      ],
    };
  },
};
</script>
